export default "import { Field } from '@cofi/react-form';\n\nconst getGrid = (templateAreas) => {\n  let fieldIds = templateAreas.join(' ').split(' ').filter(x => x !== '.');\n  fieldIds = [...(new Set(fieldIds))];\n  return {\n    templateAreas,\n    elements: fieldIds.map(id => ({ \n      selector: `#${id}`, \n      gridArea: id, \n      component: Field, \n      props: { id },\n      style: 'width: 250px;',\n    })),\n  };\n};\n\nexport default [{\n  id: 'personal-information',\n  title: 'Personal Information',\n  grid: getGrid([\n    'firstName    lastName  .',\n    'personalId   address   .',\n  ]),\n}, {\n  id: 'job-information',\n  title: 'Job Information',\n  grid: getGrid([\n    'level       benefits    .',\n    'department  benefits    .',\n  ]),\n}, {\n  id: 'raw-data',\n  title: 'Raw Data',\n  grid: getGrid([\n    'id         creationDate       .',\n    'modifier   modificationDate   .',\n  ]),\n  sections: [{\n    id: 'raw-data-general',\n    title: 'General',\n    grid: getGrid([\n      'id modifier .',\n    ]),\n  }, {\n    id: 'raw-data-modification',\n    title: 'Modification',\n    grid: getGrid([\n      'creationDate modificationDate .',\n    ]),\n  }],\n}];\n";