export default "import { Field } from '@cofi/react-form';\n\nconst column = (fieldIds) => ({\n  style: `\n    display: flex;\n    flex-direction: column;\n    width: 400px;\n    max-width: 400px;\n    margin: 0 30px 0 0;\n  `,\n  boxes: fieldIds.map(id => ({ component: Field, props: { id } })),\n});\n\nconst row = (columns) => ({\n  style: {\n    display: 'flex',\n    flexDirection: 'row',\n  },\n  boxes: columns.map(fieldIds => column(fieldIds)),\n});\n\nexport default [{\n  id: 'personal-information',\n  title: 'Personal Information',\n  boxes: [row([\n    ['firstName', 'lastName'],\n    ['personalId', 'address'],\n  ])],\n}, {\n  id: 'job-information',\n  title: 'Job Information',\n  boxes: [row([\n    ['department', 'benefits'],\n    ['level'],\n  ])],\n}, {\n  id: 'raw-data',\n  title: 'Raw Data',\n  boxes: [row([\n    ['id', 'modifier'],\n    ['creationDate', 'modificationDate'],\n  ])],\n  sections: [{\n    id: 'raw-data-general',\n    title: 'General',\n    boxes: [row([\n      ['id'],\n      ['modifier'],\n    ])],\n  }, {\n    id: 'raw-data-modification',\n    title: 'Modification',\n    boxes: [row([\n      ['creationDate'],\n      ['modificationDate'],\n    ])],\n  }],\n}];\n";